import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'changeTextDataType',
  standalone:true
})
export class ChangeTextDataTypePipe implements PipeTransform {

  transform(item: any ,currentHeader:any ,currentData:any): any {
    // Check if the items array is defined
    if (!item) {
      return [];
    }
    

    let DataType = currentHeader.datatype.split(',')[1]
    


    switch(DataType){
      case "int": item = Number.parseInt(item)
                  break;
      case "float":item = Number.parseFloat(item).toFixed(2);
                  break;
      case "string":break;

      default:  break;
    }

    return item;
  


    
  }
}
