import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';
import { AccessauthorizationService } from './services/accessauthorization/accessauthorization.service';
import { RouteGuard } from './guards/route.guard';
import { ReportDashboardPowerBIComponent } from './components/report-dashboard-power-bi/report-dashboard-power-bi.component';
import { QuickinsightComponent } from './components/quickinsight/quickinsight.component';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'login',
    pathMatch: 'full',
  },
 
  {
    path: 'dairy-farm-dashboard',
    loadChildren: () =>
      import('./pages/mymainpage/mymainpage.module').then(
        (m) => m.MymainpagePageModule
      ),
      
  },
  {
    path: 'farmer-dashboard',
    loadChildren: () =>
      import('./pages/mymainpage/mymainpage.module').then(
        (m) => m.MymainpagePageModule
      ),
      
  },
  
  {
    path: 'collector',
    loadChildren: () =>
    import('./pages/mymainpage/mymainpage.module').then(
      (m) => m.MymainpagePageModule
    ),
   
  },
  {
    path: 'multiple-select-modal',
    loadChildren: () =>
      import('./pages/multiple-select-modal/multiple-select-modal.module').then(
        (m) => m.MultipleSelectModalPageModule
      ),
  },
  {
    path: 'single-select-modal',
    loadChildren: () =>
      import('./pages/single-select-modal/single-select-modal.module').then(
        (m) => m.SingleSelectModalPageModule
      ),
  },
  
  {
    path: 'login',
    loadChildren: () =>
      import('./pages/login/login.module').then((m) => m.LoginPageModule),
  },
  // {
  //   path: 'login',
  //   loadChildren: () =>
  //     import('./pages/mymainpage/mymainpage.module').then((m) => m.MymainpagePageModule),
  // },

  {
    path: 'forgot-password',
    loadChildren: () =>
      import('./pages/forgot-password/forgot-password.module').then(
        (m) => m.ForgotPasswordPageModule
      ),
  },
  {
    path: 'chilling-centre-dashboard',
    loadChildren: () =>
      import(
        './pages/mymainpage/mymainpage.module'
      ).then((m) => m.MymainpagePageModule),
  },
 

  {
    path: 'signup',
    loadChildren: () =>
      import('./pages/signup/signup.module').then((m) => m.SignupPageModule),
  },
  
 
  {
    path: 'customreports',
    loadChildren: () => import('./pages/customreports/customreports.module').then( m => m.CustomreportsPageModule),
    
  },
  {
    path: 'customreportdetail',
    loadChildren: () => import('./pages/customreportdetail/customreportdetail.module').then( m => m.CustomreportdetailPageModule)
    
  },
  {
    path: 'mytest',
    loadChildren: () => import('./pages/mytest/mytest.module').then( m => m.MytestPageModule)
  },
  {
    path: 'mytest2',
    loadChildren: () => import('./pages/mytest2/mytest2.module').then( m => m.Mytest2PageModule)
  },
  {
    path:'powerbi',
    component:ReportDashboardPowerBIComponent
  },
  {
    path:'quick',
    component:QuickinsightComponent
  }

  // {
  //   path: 'powerbi',
  //   loadChildren: () => import('./pages/powerbi/powerbi.module').then( m => m.PowerbiPageModule)
  // }




];
@NgModule({
  imports: [
    RouterModule.forRoot(routes, {
      preloadingStrategy: PreloadAllModules,
    }),
  ],
  exports: [RouterModule],
})
export class AppRoutingModule {}
