  validateLength(value: string, start: Number, end: Number) {
    if (value.match('^.{' + start + ',' + end + '}$')) {
      return true;
    } else {
      return false;
    }
  }

  formatString(str: string, args: any[]) {
    return str.replace(/{([0-9]+)}/g, (match, index) => {
      return typeof args[index] == 'undefined' ? match : args[index];
    });
  }

