  exportPdfmultipledata(dataToExport: any[][], filename = 'doc.pdf') {
    if (!dataToExport && dataToExport.length === 0) {
      return null;
    }
    const doc = new jsPDF('l', 'pt', 'a4');
    let headings = [];

    dataToExport.forEach((data, index) => {
      const objWithHeading = data.find((item) => !!item.heading);
      const objWithoutHeading = data.filter((item) => !item.heading);
      headings.push(objWithHeading.heading);
      dataToExport[index] = objWithoutHeading;
    });

    for (let i = 0; i < dataToExport.length; ++i) {
      const rows = dataToExport[i].map((item) => (Object as any).values(item));
      const columns = Object.keys(dataToExport[i][0]);
      let yPos = 35;
      if (i !== 0) yPos = (doc as any).autoTable.previous.finalY + 15;
      (doc as any).text(headings[i], 40, yPos);

      if (i === 0) {
        (doc as any).autoTable(columns, rows, {
          styles: {
            overflow: 'linebreak',
            cellWidth: 'wrap',
          },
        });
      } else {
        (doc as any).autoTable(columns, rows, {
          styles: {
            overflow: 'linebreak',
            cellWidth: 'wrap',
          },
          margin: { bottom: 100 },
        });
      }
    }
    doc.save(filename);
    doc.close();
  }

  exportPdf(dataToExport: any[][any], filename = 'doc.pdf') {
    if (!dataToExport && dataToExport.length === 0) {
      return null;
    }
    const doc = new jsPDF('l', 'pt', 'a4');
    autoTable(doc, {
      head: [Object.keys(dataToExport[0])],
      body: [...dataToExport.map((item) => (Object as any).values(item))],
    });
    doc.save(filename);
  }
