import { Component, OnInit } from '@angular/core';
import { NavController, ModalController, Platform } from '@ionic/angular';
import { AppVersion } from '@ionic-native/app-version/ngx';
import { BaseComp } from 'src/app/utils/BaseComp';
import { NativeAudio } from '@ionic-native/native-audio/ngx';
import { Vibration } from '@ionic-native/vibration/ngx';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-pop-up-notification',
  templateUrl: './pop-up-notification.component.html',
  styleUrls: ['./pop-up-notification.component.scss'],
})
export class PopUpNotificationComponent extends BaseComp implements OnInit {
  constructor(
    private appVersion: AppVersion,
    public navCtrl: NavController,
    private nativeAudio: NativeAudio,
    public viewCtrl: ModalController,
    private platform: Platform,
    private vibration: Vibration,
    private route: ActivatedRoute
  ) {
    super();
  }
  title: string = '';
  text: string = '';
  initPage() {
    console.log('ionViewDidLoad NotificationPopupPage');
    if (this.platform.is('android')) {
      this.playAndrooidSound();
    } else {
      this.playIosSound();
    }
    this.vibration.vibrate(1000);
    this.route.queryParams.subscribe((params) => {
      console.log('params constructor: ', params);
      if (params && params.title) {
        this.title = JSON.parse(params.title);
        console.log('title constructor: ' + this.title);
      }
      if (params && params.text) {
        this.text = JSON.parse(params.text);
        console.log('text constructor: ' + this.text);
      }
    });

    console.log('text', this.text);
    console.log('title', this.title);
    setTimeout(() => {
      this.dismiss();
    }, 4000);
  }

  dismiss() {
    this.viewCtrl.dismiss('dismissed');
  }
  appname: string;
  ionViewWillEnter() {
    this.initPage();
    this.appname = this.getItem('appName');
    // this.soundLoad() ;
  }

  // soundLoad(){
  //   this.nativeAudio.preloadSimple('ios', 'assets/audio/ios_notofication.mp3').then((success)=>{
  //     console.log("success") ;
  //   },(error)=>{
  //     console.log(error) ;
  //   }) ;
  //   this.nativeAudio.preloadSimple('android', 'assets/audio/android_notifiication.mp3').then((success)=>{
  //     console.log("success") ;
  //   },(error)=>{
  //     console.log(error) ;
  //   }) ;

  // }
  playIosSound() {
    try {
      this.nativeAudio.play('ios').then(
        (success) => {
          console.log('success playing');
        },
        (error) => {
          console.log(error);
        }
      );
    } catch (e) {
      console.log('playSound error', e);
    }
  }
  playAndrooidSound() {
    try {
      this.nativeAudio.play('android').then(
        (success) => {
          console.log('success playing');
        },
        (error) => {
          console.log(error);
        }
      );
    } catch (e) {
      console.log('playSound error', e);
    }
  }

  ngOnInit() {}
}
