import { Injectable } from '@angular/core';
import * as CryptoJS from 'crypto-js';
import * as JsEncrypt from 'jsencrypt';
import { BaseComp } from 'src/app/utils/BaseComp';

@Injectable({
  providedIn: 'root'
})
export class EncryptionService extends BaseComp {

  constructor() 
  {
    super()
  
  }

  Encrypt(value:any){       
      
    var key = CryptoJS.enc.Utf8.parse(this.AesSecretIVKey);
    var iv = CryptoJS.enc.Utf8.parse(this.AesSecretIVKey);    
    var encrypted = CryptoJS.AES.encrypt(CryptoJS.enc.Utf8.parse(value.toString()), key,
    {
        keySize: 128 / 8,
        iv: iv,
        mode: CryptoJS.mode.CBC,
        padding: CryptoJS.pad.Pkcs7
    });
    return encrypted.toString().replace(/[/\ : ]/g , 'Por21Ld').replace(/[+ : ]/g , '!');
  }

  //The get method is used to decrypt the value.
  Decrypt(value:any){
    value=value.replace(/Por21Ld/g, '/').replace(/!/g, '+');
    var key = CryptoJS.enc.Utf8.parse(this.AesSecretIVKey);
    var iv = CryptoJS.enc.Utf8.parse(this.AesSecretIVKey);
    var decrypted = CryptoJS.AES.decrypt(value, key, {
        keySize: 128 / 8,
        iv: iv,
        mode: CryptoJS.mode.CBC,
        padding: CryptoJS.pad.Pkcs7
    });

    return decrypted.toString(CryptoJS.enc.Utf8);
  }

   EncryptTextWithPublicKey(text:any,publicKey:any){
    let encrypt = new JsEncrypt.JSEncrypt();
    encrypt.setPublicKey(publicKey);
    return encrypt.encrypt(text)
  }

}
