  private initPushNotification(): Promise<string> {
    console.error("Here 3")
    return new Promise<string>((resolve, reject) => {
      if (!this.platform.is('cordova')) {
        console.warn(
          'Push notifications not initialized. Cordova is not available - Run in physical device'
        );
        resolve('');
      }
     
      let deviceDetails = {
        appVersion: this.getItem('AppVersion'),
        manufacturer: this.device.manufacturer,
        model: this.device.model,
        deviceVersion: this.device.version,
        platform: this.device.platform,
        serial: this.device.serial,
        uuid: this.device.uuid,
        cordova: this.device.cordova,
        isVirtual: this.device.isVirtual,
      };
      this.saveItem('Device_details', JSON.stringify(deviceDetails));
      try {
        this.onNotificationOpen().subscribe(async (data: any) => {
          //if user using app and push notification comes
          if (data.title == 'Update Available') {
            if (!data.show_notification) {
              // if application open, show popup
              let confirmAlert = this.alertCtrl.create({
                header: data.title,
                mode: 'ios',
                message: data.body,
                buttons: [
                  {
                    text: 'Ok',
                    role: 'ok',
                  },
                ],
              });
              (await confirmAlert).present();
            } else {
              let confirmAlert = await this.alertCtrl.create({
                header: data.title,
                mode: 'ios',
                message: data.body,
                buttons: [
                  {
                    text: 'Ok',
                    role: 'ok',
                  },
                ],
              });
              confirmAlert.present();
            }
          }

          if (data.show_notification) {
            if (this.platform.is('android')) {
              this.foreNotif(data.title, data.body);
            } else {
              // if (data.body != undefined) {
              //   //this.showtoast(data.additionalData.text);
              //   // if ((data.additionalData.progressBar.enabled = false)) {
              //   //   this.presentModal(
              //   //     data.title,
              //   //     data.additionalData.text
              //   //   );
              //   // }
              // } else {
              this.presentModal(data.title, data.body);
              // }
            }
          }
        });
      } catch (err) {
        console.error('this.onNotificationOpen() error', err);
      }
      console.error("Here 4");
      (<any>window).FirebasePlugin.getToken(
        (token) => {
          console.error(token,"recieved token")
          this.saveItem('device_token', token);
          resolve(token);
        },
        (error) => {
          reject(error);
          console.error(error,"in this error")
        }
      );
    });
  }

  async showtoast(msg) {
    const toast = await this.toastCtrl.create({
      message: msg,
      duration: 20000,
      cssClass: 'toast_notification',
      position: 'top',
      buttons: [
        {
          text: 'Done',
          role: 'cancel',
          handler: () => {
          },
        },
      ],
      // showCloseButton: true,
      // closeButtonText: " ",
    });
    toast.present();
  }
  modelActive: boolean = false;
  modal: any;
  async presentModal(title, text) {
    if (this.modelActive) {
      this.modal.dismiss();
    }
    this.modal = await this.modalController.create({
      component: PopUpNotificationComponent,
      componentProps: {
        title: title,
        text: text,
      },
      cssClass: 'notification-card',
      // enterAnimation: "modal-scale-up-enter",
      // leaveAnimation: "modal-scale-up-leave",
    });

    this.modal.onDidDismiss((data) => {
      this.modelActive = false;
    });
    this.modelActive = true;
    return await this.modal.present();
  }

  getFarmLogoToShow() {
    try {
      if (this.Farm_Logo_Path != 'null') {
        this.base64Data = this.getItem('Farm_Logo').replace(/['"]+/g, '');
        this.converted_image = this.sanitizer.bypassSecurityTrustUrl(
          'data:image/jpeg;base64,' + this.base64Data
        );
        this.show_farm_logo = true;
        this.changeDetectorRef.detectChanges();
      }
    } catch (e) {
      console.error('error function getFarmLogoToShow AppComponent Page:- ,', e);
    }
  }

