import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree } from '@angular/router';
import { resolve } from 'dns';
import { Observable, Subscription, interval } from 'rxjs';
import { BaseComp } from 'src/app/utils/BaseComp';
import { TokenService } from '../token/token.service';
import { AlertController, NavController } from '@ionic/angular';

@Injectable({
  providedIn: 'root'
})
export class AccessauthorizationService extends BaseComp {

  
  passCheck:Subscription
  acessAuthUrlIdentifier = `api/servicesAPI/testaccess`
  

  constructor(public http: HttpClient,private token: TokenService,
              public alertCtrl: AlertController,private nav : NavController)
   {
    super();
    }

     
    async checkServiceAcessFunction(serviceIdinput:number){
      let body= {id:1}

      if(serviceIdinput==null || serviceIdinput==undefined){
        return
      }
      else{

      
      let url_accessAuthCheck=this.BASE_URL+this.acessAuthUrlIdentifier+'?UserId='+this.getFarmerUserID()
                           +'&serviceId='+serviceIdinput+
                           '&Key='+this.key+'&con='+this.DatabaseCode
      
         
           
            await this.http.post(url_accessAuthCheck,body).subscribe(async m=>{
              if(m==0)
              {
                const alert = this.alertCtrl.create({
                  message:"Restricted Resource,Contact Administrator",
                  buttons:([{
                    text:"Ok",
                    role:"Ok"
                  }])
                });
                (await alert).present();
                return this.nav.pop()
              }
            else if(m==1)
             {

             }
            else
               console.error("Service Check Failed")
            })
            
       
         
     
    }
  }

  async checkForPasswordChangeFunction ():Promise<boolean>{
    console.log("Checking For Pass Change")
    const identifier = 'api/servicesAPI/checkForPassChange'
    let url_check_For_Password_Change= this.BASE_URL+identifier+'?UserId='+this.getFarmerUserID()
                                        +'&Key='+this.key
                                        +'&con='+ await this.getItem('Db_code')
    let data ={}

    return new Promise<boolean>((resolve,reject)=> {
      this.http.get(url_check_For_Password_Change,data).subscribe(
        m =>{
          
          if(m["ErrorCode"]==401){
            reject(m["ErrorMessage"])
          }
          resolve(m["toLogout"])
          
           
        }
      )
    })

    
  }

  async startPasswordCheckService(start:boolean){
      if(start==true){
        this.passCheck=interval(this.check_For_PassChange_interval).subscribe(async x =>{
            try {
                let passChecked = await this.checkForPasswordChangeFunction()
                if(passChecked){
                  this.token.removeToken();
                  this.token.removeAuthToken();
                  this.token.removeRefreshAccessToken();
                  this.removeItem('Db_code');
                  this.removeItem(this.LOGIN_USER_DATA);
                  this.removeItem(this.IS_LOGIN);
                  const alert=await this.alertCtrl.create({
                    message:"You Have Been Logged Out , Login Again",
                    buttons:["Ok"]
                  })
                  await alert.present()
                  this.nav.navigateRoot('login')
                  if(this.passCheck){
                    this.passCheck.unsubscribe()
                   this.passCheck.remove(this.passCheck)
                  }
                }
                else{
                  
                }
                } catch (error) {
                console.error("Unable To Check for Password Change",error)
              }
        })
        
          }
      else{
          if(this.passCheck){
            this.passCheck.unsubscribe()
           this.passCheck.remove(this.passCheck)
          }
      }
  }

 

}
