import { Injectable } from '@angular/core';
import { ModalController, ToastController } from '@ionic/angular';
import { Network } from '@ionic-native/network/ngx';
import { BehaviorSubject } from 'rxjs';
import { Observable } from 'rxjs';
import { BaseComp } from '../../utils/BaseComp';
import { StartUploadingSeriviceService } from '../start-uploading-serivice/start-uploading-serivice.service';
import { LocalNotifications } from '@ionic-native/local-notifications/ngx';
import { HttpClient } from '@angular/common/http';
import { EventsService } from '../events/events.service';
import { PopUpNotificationComponent } from 'src/app/components/pop-up-notification/pop-up-notification/pop-up-notification.component';
export enum ConnectionStatus {
  Online,
  Offline,
}
declare var cordova;

@Injectable({
  providedIn: 'root',
})
export class NetworkServiceService extends BaseComp {
  public status: ConnectionStatus;
  private _status: BehaviorSubject<ConnectionStatus> = new BehaviorSubject(
    null
  );

  constructor(
    public localNotifications: LocalNotifications,
    public network: Network,
    private modalController: ModalController,
    private UploadingSerivice: StartUploadingSeriviceService,
    public http: HttpClient,
    public toastCtrl: ToastController,
    public events: EventsService
  ) {
    super();
    this.status = ConnectionStatus.Online;
  }

  public initializeNetworkEvents(): void {
    /* OFFLINE */
    this.network.onDisconnect().subscribe(() => {
      if (this.status === ConnectionStatus.Online) {
        this.setStatus(ConnectionStatus.Offline);
        // this.foreNotif("Internet disconnected");
        this.presentModal('', 'Internet disconnected');
        this.events.publish('networkStatus_changed', 1);
      }
    });

    /* ONLINE */
    this.network.onConnect().subscribe(() => {

      this.isConnectedToInternet().then((value) => {
        if (value) {
          if (this.status === ConnectionStatus.Offline) {
            //this.foreNotif("Internet Connected") ;

            this.setStatus(ConnectionStatus.Online);
            this.presentModal("", "Internet Connected");
            this.events.publish("networkStatus_changed", 0);
          }
        } else {
          this.setStatus(ConnectionStatus.Offline);
          this.presentModal("", "Network does not have internet connection");
          this.events.publish("networkStatus_changed", 1);
        }
      });
    });
  }

  /**
   * This method calls an api and if api returns any value then user is connected to
   * interent, else not
   * @returns boolean (if connected then true else false)
   */
  public isConnectedToInternet() {
    return new Promise((resolve, reject) => {
      const url =
        this.BASE_URL + 'api/GeneralDataApi/getInternetConnectionStatus';
      this.http.get(url).subscribe(
        (result) => {
          resolve(result);
        },
        (error) => {
          console.error('error in check internet:', error);
          resolve(false);
        }
      );
    });
    // try {
    //   const url =
    //     this.BASE_URL + "api/GeneralDataApi/getInternetConnectionStatus";
    //   const response = await this.http.get(url).toPromise();
    //   return true;
    // } catch (err) {
    //   this.somethingWentWrongError(this.toastCtrl);
    //   return false;
    // }
  }

  modelActive: boolean = false;
  modal: any;
  async presentModal(title, text) {
    if (this.modelActive && this.modal) {
      this.modal.dismiss();
    }
    this.modal = await this.modalController.create({
      component: PopUpNotificationComponent,
      componentProps: {
        title: title,
        text: text,
      },
      cssClass: 'notification-card',
      // enterAnimation: "modal-scale-up-enter",
      // leaveAnimation: "modal-scale-up-leave",
    });
    // this.modal = await this.modalController.create(
    //   NotificationPopupPage,
    //   {
    //     title: title,
    //     text: text,
    //   },
    //   {
    //     cssClass: "notification-card",
    //     enterAnimation: "modal-scale-up-enter",
    //     leaveAnimation: "modal-scale-up-leave",
    //   }
    // );
    this.modal.onDidDismiss((data) => {
      this.modelActive = false;
    });
    this.modelActive = true;
    return await this.modal.present();
  }

  foreNotif(msg) {
    cordova.plugins.notification.local.schedule({
      id: 1,
      text: msg,
      data: { secret: 'secret' },
      smallIcon: 'res://mipmap-ldpi/ic_launcher.png',
      foreground: true,
      sound: false,
    });
  }
  public getNetworkType(): string {
    return this.network.type;
  }
  public getNetworkDownSpeed(): string {
    return this.network.downlinkMax;
  }
  public getNetworkStatus(): Observable<ConnectionStatus> {
    return this._status.asObservable();
  }

  private setStatus(status: ConnectionStatus) {
    this.status = status;
    this.saveItem("InternetStatus", this.status);
    this._status.next(this.status);
  }
}
