  async DCFOfflineApi(
    json: any,
    fedrationId,
    Dcf_id,
    key,
    dbcode,
    uploded,
    total
  ) {
    switch (fedrationId) {
      case 6: {
        await this.callDcfOfflineApiForGyan(
          json,
          Dcf_id,
          key,
          dbcode,
          uploded,
          total
        );
        break;
      }
      default: {
        await this.callDcfOfflineApi(json, Dcf_id, key, dbcode, uploded, total);
        break;
      }
    }
  }

  async callDcfOfflineApi(json: any, Dcf_id, key, dbcode, uploded, total) {
    let postData = new FormData();
    // let ApiUrl="api/DataCollectionFarmerApi/DCFoffline?dataCollectionFarmerModelDetail=" ;
    let url =
      this.BASE_URL +
      'api/DataCollectionFarmerApi/DCFoffline?dataCollectionFarmerModelDetail=' +
      JSON.stringify(json) +
      '&Key=' +
      key +
      '&con=' +
      dbcode;

    console.log('url:-' + url);
    try {
      if (this.getItem('InternetStatus') == '1') {
        console.log('no internet connectivity');
        //this.localNotifications.clear(2) ;
        // this.foreNotif("Uploding Intrrupted","Inernet connectivity get lost")
        console.log(
          'network status 1 means offline: ',
          this.getItem('InternetStatus')
        );
      } else {
        await this.http
          .post(url, postData)
          .toPromise()
          .then(
            (result) => {
              //loading.dismiss() ;
              try {
                console.log('result:-' + JSON.stringify(result));
                var result = result;
                if (
                  result == 'True' ||
                  result == 'Already Exist' ||
                  result == 'Insert Successfully'
                ) {
                  this.db.updateDCFOfflineUploadTrue(Dcf_id);
                  this.Uplodedsuccessfully += 1;
                  this.progressNotif(this.Uplodedsuccessfully, total, 2);
                  console.log(uploded, ' , ', total);
                } else {
                  this.notgetUploded += 1;
                }
              } catch (e) {
                console.log(e);
                this.notgetUploded += 1;
              }
            },
            (error) => {
              //loading.dismiss() ;
              console.log('API Error', error);
              this.notgetUploded += 1;
              // this.somethingWentWrongError(this.toastCtrl) ;
            }
          );
      }
    } catch (er) {
      console.log('DCF offline api error', er);
    }
  }

  async callDcfOfflineApiForGyan(
    json: any,
    Dcf_id,
    key,
    dbcode,
    uploded,
    total
  ) {
    // insert Gyan integration api here example if dATA NEED to send at everest server then isert that api here

    await this.callDcfOfflineApi(json, Dcf_id, key, dbcode, uploded, total); // call api to insert data in qboid db
  }

  DcfofflineRecords: dcfoffline_data[] = [];
  notgetUploded: number = 0;
  Uplodedsuccessfully: number = 0;
  HitAllDcfOfflineRecords() {
    console.log('HitAllDcfOfflineRecords start-uploading');
    this.DcfofflineRecords = [];
    this.Progressvalue = 0;
    this.notgetUploded = 0;
    this.Uplodedsuccessfully = 0;
    // this.db.getAllDCFunuplodedRecordsForOfflineUpload()
    return new Promise((resolve, reject) => {
      this.db
        .getAllDCFunuplodedRecordsForOfflineUpload()
        .then(async (res: any) => {
          console.log('db dcf: ' + res);
          console.log('db dcf.rows.length : ' + res.rows.length);

          for (let i = 0; i < res.rows.length; i++) {
            const Dcf_Json: any = JSON.parse(res.rows.item(i).json);
            console.log('json parse: ', Dcf_Json);

            const OfflineData: dcfoffline_data = {
              Dcf_id: res.rows.item(i).ID,
              json: Dcf_Json,
              uploded: res.rows.item(i).uploded,
              fedrationId: res.rows.item(i).fedrationId,
              key: res.rows.item(i).key,
              DatabaseCode: res.rows.item(i).dbcode,
            };
            this.DcfofflineRecords.push(OfflineData);
            if (this.getItem('InternetStatus') == '1') {
              break;
            } else {
              await this.DCFOfflineApi(
                OfflineData.json,
                OfflineData.fedrationId,
                OfflineData.Dcf_id,
                OfflineData.key,
                OfflineData.DatabaseCode,
                i + 1,
                res.rows.length
              );
            }
          }
          if (res.rows.length > 0) {
            if (
              this.notgetUploded < res.rows.length &&
              this.notgetUploded > 0 &&
              this.Uplodedsuccessfully > 0
            ) {
              cordova.plugins.notification.local.clear(2);
              this.foreNotif(
                'Upload successfully',
                '' +
                  this.Uplodedsuccessfully +
                  ' records uploded successfully. Try manual sink for others after some time',
                1
              );
            } else if (
              this.notgetUploded == 0 &&
              this.Uplodedsuccessfully > 0
            ) {
              cordova.plugins.notification.local.clear(2);
              this.foreNotif(
                'Upload successfully',
                '' +
                  this.Uplodedsuccessfully +
                  ' records uploded successfully.',
                1
              );
            } else {
              cordova.plugins.notification.local.clear(2);
              this.foreNotif(
                'Try Manual sink after some time',
                '' +
                  (res.rows.length - this.Uplodedsuccessfully) +
                  ' records get failed to upload. Try manual sink after some time',
                1
              );
            }
          }
        })
        .then(() => {
          resolve(1);
        })
        .catch((error) => {
          console.log('getany error: ', error);
          reject();
        });
    });
  }

