  async chemicalTestOfflineApi(
    json,
    fedrationId,
    chemicalTestId,
    key,
    dbCode,
    uploaded,
    total
  ) {
    await this.callChemicalTestingOfflineApi(
      json,
      chemicalTestId,
      key,
      dbCode,
      uploaded,
      total
    );
  }

  async callChemicalTestingOfflineApi(
    json,
    chemicalTestId,
    key,
    dbcode,
    uploaded,
    total
  ) {
    const postData = new FormData();
    const dataArray = [];
    json.tag = 'offline';
    dataArray.push(json);
    const url =
      this.BASE_URL +
      'api/DataCollectionFarmerApi/addChemicalTest' +
      '?ChemicalTestDetails=' +
      JSON.stringify(dataArray) +
      '&key=' +
      key +
      '&con=' +
      dbcode;
    console.log('url: ', url);
    console.log('called from offline chemical testing function...');
    if (this.getItem('InternetStatus') == '1') {
      console.log('no internet connectivity');
      //this.localNotifications.clear(2);
      // this.foreNotif("Uploding Intrrupted","Inernet connectivity get lost")
      console.log(
        'network status 1 means offline: ',
        this.getItem('InternetStatus')
      );
    } else {
      await this.http
        .post(url, postData)
        .toPromise()
        .then(
          async (res) => {
            try {
              console.log('result: ', JSON.stringify(res));
              let result = res;
              if (
                result == 'True' ||
                result == 'Already Exist' ||
                result == 'Insert Successfully'
              ) {
                await this.db.updateChemicalTestingOfflineUploadTrue(
                  chemicalTestId
                );
                this.Uplodedsuccessfully = this.Uplodedsuccessfully + 1;
                console.log(
                  '##########uploaded succesfully: ',
                  this.Uplodedsuccessfully,
                  '###########'
                );
                this.progressNotif(this.Uplodedsuccessfully, total, 3);
                console.log('uploaded ', uploaded, 'out of', total);
              } else {
                this.notgetUploded += 1;
              }
            } catch (err) {
              console.log(err);
              this.notgetUploded += 1;
            }
          },
          (error) => {
            //loading.dismiss() ;
            console.log('API Error', error);
            this.notgetUploded += 1;
            // this.somethingWentWrongError(this.toastCtrl) ;
          }
        )
        .catch((err) => {});
    }
  }

