import { Injectable } from '@angular/core';
import { HttpClient } from "@angular/common/http";
import { SqlService } from "../sql/sql.service";
import { EdpuServerService } from "../edpu-server/edpu-server.service";
import { AlertController } from "@ionic/angular";
import { BaseComp } from "../../utils/BaseComp";
import { dcfoffline_data } from "../interfaces/interfaces.service";
import { LocalNotificationService } from "../local-notification/local-notification.service";

@Injectable({
  providedIn: 'root'
})
export class DcfDataTransferOverLocalNetworkService extends BaseComp{
  constructor(
    public http: HttpClient,
    private dbProvider: SqlService,
    private edpuserver: EdpuServerService,
    private alertCtrl: AlertController,
    private edpuServer: EdpuServerService,
    private localNotification: LocalNotificationService,
  ) {
    super();
    console.log("Hello DcfDataTransferOverLocalNetworkProvider Provider");
  }

  subscribeDcfUnploadedDataTransfer() {
    // this.events.subscribe("DcfUnploadedDataTransfer", (uuid) => {
    //   console.log("DcfUnploadedDataTransfer:-" + uuid);
    //   this.getdcfdata(uuid);
    // });
  }

  getdcfdata(uuid) {
    // this.DcfofflineRecords = [] ;
    // this.Progressvalue = 0 ;
    // this.notgetUploded = 0 ;
    // this.Uplodedsuccessfully = 0 ;
    let records: any;
    this.dbProvider.getAllDCFunuplodedRecordsForOfflineUpload().then(
      (res: any) => {
        console.log(
          "db getAllDCFunuplodedRecordsForOfflineUpload in dcf transfer: " + res
        );
        console.log("db dcf.rows.length : " + res.rows.length);

        records = res;
        if (records.rows.length > 0) {
          this.pendingUnuploadedDCFdataAlert(records, uuid);
        }

        // this.gaugemax = res.rows.length ;
        // this.gaugeAllRecordvalue = res.rows.length ;
      },
      (error) => {
        console.log("getDcf_data error: ", error);
      }
    );
  }

  async pendingUnuploadedDCFdataAlert(records, uuid) {
    let alert = await this.alertCtrl.create({
      header: "Un-sync Data",
      mode: "ios",
      subHeader: "You have un-sync data, Do you want to sync?",
      buttons: [
        {
          text: "Yes",
          handler: () => {
            this.startSendingRecords(records, uuid);
            console.log("Yes clicked");
          },
        },
        {
          text: "No",
          role: "cancel",
          handler: () => {
            console.log("cancel clicked");
          },
        },
      ],
    });
    alert.present();
  }

  async startSendingRecords(res, uuid) {
    // this.DcfofflineRecords = [] ;
    // this.Progressvalue = 0 ;
    // this.notgetUploded = 0 ;
    // this.Uplodedsuccessfully = 0 ;
    // this.db.getAllDCFunuplodedRecordsForOfflineUpload()
    console.log("db dcf: " + res);
    console.log("db dcf.rows.length : " + res.rows.length);

    for (let i = 0; i < res.rows.length; i++) {
      const Dcf_Json: any = JSON.parse(res.rows.item(i).json);
      console.log("json parse: ", Dcf_Json);

      const OfflineData: dcfoffline_data = {
        Dcf_id: res.rows.item(i).ID,
        json: res.rows.item(i).json,
        User_id: res.rows.item(i).User_id,
        url: res.rows.item(i).url,
        uploded: res.rows.item(i).uploded,
        fedrationId: res.rows.item(i).fedrationId,
        key: res.rows.item(i).key,
        DatabaseCode: res.rows.item(i).dbcode,
      };
      console.log("OfflineData ", OfflineData);
      //this.DcfofflineRecords.push(OfflineData) ;

      await this.callDataTransferApi(OfflineData, i + 1, res.rows.length, uuid);
    }
  }
  async callDataTransferApi(OfflineData: dcfoffline_data, sended, total, uuid) {
    let data =
      "Transporter@OfflineDcfDataTransfer@" +
      OfflineData.json +
      "@" +
      OfflineData.fedrationId +
      "@" +
      OfflineData.uploded +
      "@" +
      OfflineData.key +
      "@" +
      OfflineData.DatabaseCode +
      "@" +
      OfflineData.url +
      "@" +
      OfflineData.User_id +
      "@" +
      sended +
      "@" +
      total;
    this.edpuServer.websocket8889SendData(uuid, data);
    if (sended != total) {
      this.localNotification.showProgressNotification(
        sended,
        total,
        3,
        "Data Sync",
        "Data syncing in progress"
      );
    } else {
      this.localNotification.showProgressNotification(
        sended,
        total,
        3,
        "Data Sync",
        "Data syncing in progress"
      );
      setTimeout(() => {
        this.localNotification.clearNotificationBanner(3);
        this.localNotification.foreNotif(
          "Data Sync",
          "Data Sync completed!",
          2
        );
      }, 500);
    }
  }
}

