  dbName: SQLiteObject;
  createDatabase() {
    try {
      return this.sqlite
        .create({
          name: "Qboid.db",
          location: "default",
        })
        .then(
          (db: SQLiteObject) => {
            this.dbName = db;
          },
          (error) => {
            console.error("createDatabase error: ", error);
          }
        );
    } catch (er) {
      console.log("createDatabase error: ", er);
    }
  }

  VersionDatabase(version) {
    try {
      return this.sqlite
        .create({
          name: "Qboid.db",
          location: "default",
        })
        .then(
          (db: SQLiteObject) => {
            this.dbName = db;
            console.log("dbName error: ", this.dbName);
            this.checkAppVersion(version, db);
          },
          (error) => {
            console.error("createDatabase error: ", error);
          }
        );
    } catch (er) {
      console.error("createDatabase error: ", er);
    }
  }
  //## check api version and add changes data
  checkAppVersion(version, db) {
    switch (version) {
      case "1.0.6":
      case "1.0.7":
      case "1.0.8":
      case "1.0.9":
      case "1.0.10": {
        let query = [
          "DROP TABLE DCF",
          "DROP TABLE WeightRecords",
          "DROP TABLE Farmer",
          "DROP TABLE PriceProfile",
          "DROP TABLE CollectionCentre",
        ];
        this.alterTableComands(query);
        break;
      }
      case "1.2.3":
      case "1.2.4":
      case "1.2.5": {
        let query = [
          "DROP TABLE DCF",
          "DROP TABLE WeightRecords",
          "DROP TABLE Farmer",
          "DROP TABLE PriceProfile",
          "DROP TABLE CollectionCentre",
          "DROP TABLE priceprofileorganizationmapping",
        ];
        this.alterTableComands(query);
        break;
      }
      case "1.2.9":
      case "1.3.0": {
        let query = [
          "DROP TABLE DCF",
          "DROP TABLE WeightRecords",
          "DROP TABLE Farmer",
          "DROP TABLE PriceProfile",
          "DROP TABLE CollectionCentre",
          "DROP TABLE priceprofileorganizationmapping",
        ];
        this.alterTableComands(query);
        break;
      }
      case "1.3.1":
      case "1.3.2":
      case "1.3.3":
      case "1.3.4":
      case "1.3.5": {
        let query = [
          "DROP TABLE priceprofileorganizationmapping",
          "DROP TABLE Farmer",
        ];
        this.alterTableComands(query);
        break;
      }
      case "1.3.7":
      case "1.4.0":
      case "1.5.2": {
        let query = [
          "DROP TABLE Dispatch",
          "DROP TABLE priceprofileorganizationmapping",
          "DROP TABLE Farmer",
        ];
        this.alterTableComands(query);
        break;
      }
      case "1.5.3":
      case "1.5.4":
      case "1.5.5":
      case "1.5.6":
      case "1.5.7": {
        let query = [
          "DROP TABLE priceprofileorganizationmapping",
          "DROP TABLE Farmer",
        ];
        this.alterTableComands(query);
        break;
      }
      default: {
        this.createTable();
        break;
      }
    }
  }

  alterTableComands(query) {
    try {
      this.dbName.open().then((res) => {
        this.dbName.sqlBatch(query).then(
          (res) => {
            console.log(res);
            this.createTable();
          },
          (error) => {
            console.log("TABLE error: ", error);
            this.createTable();
          }
        );
      });
    } catch (er) {
      console.log("TABLE error: ", er);
      this.createTable();
    }
  }

