  //##To Get All Records of PriceProfileScheme table for a paticular user
  getAllPriceProfileSchemesTableData(userid) {
    try {
      return new Promise((resolve, reject) => {
        this.dbName.open().then(
          () => {
            let query =
              "SELECT  pps.SchemeID , pps.SchemeName ,pps.SchemeType , pps.SchemeDescription , pps.BenefitPerLitre , pps.DeductionPerLitre , ppoms.status,ppoms.usertype_id ,ppoms.organization_id ,ppoms.startDate ,ppoms.endDate ,ppoms.startShiftTime ,ppoms.endShiftTime   FROM 'priceprofilescheme' pps  inner join priceprofileschemeorganizationmapping ppoms on ppoms.scheme_ID = pps.SchemeID where  pps.UserID=" +
              userid +
              "";
            this.dbName
              .executeSql(query, [])
              .then((res) => {
                console.log(res);
                resolve(res);
              })
              .catch((err) => {
                reject(err);
              });
          },
          (error) => {
            console.log("getAllPriceProfile2TableData error: ", error);
          }
        );
      });
    } catch (er) {
      console.log("getAllPriceProfile2TableData error: ", er);
    }
  }

