import { DatePipe } from '@angular/common';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { BaseComp } from 'src/app/utils/BaseComp';

@Injectable({
  providedIn: 'root'
})
export class ReportService extends BaseComp{

  private reportToDisplay = new BehaviorSubject<any>({id:'',reportname: ''});
  activeReport = this.reportToDisplay.asObservable()

  private reportToDisplayArray = new BehaviorSubject<any[]>([{id:'',reportname: ''}]);
  activeReportArray = this.reportToDisplayArray.asObservable()

  constructor(private http:HttpClient,) { 
    super()
  }

  changeActiveReportFunction({id,reportname}){
    this.reportToDisplay.next({id,reportname})
   }

   changeActiveReportArrayFunction(change: any[]){
    console.log("Changing Reports")
    this.reportToDisplayArray.next(change)
   }

   getStaticReports(key: string, DatabaseCode: string, userId: string): Observable<any> {
   
    let url = this.BASE_URL+'api/ReportApi/getAllMappedReportByUserid?Userid='+userId+ "&Key=" +
    key +
    "&con=" +
    DatabaseCode;

    // let url_encrypted = this.BASE_URL+'api/ReportApi/getAllMappedReportByUserid?Userid='+this.EncryptionService.Encrypt(userId)+ "&Key=" +
    // this.EncryptionService.Encrypt(key) +
    // "&con=" + this.EncryptionService.Encrypt(DatabaseCode);
    
    
    
    return this.http.get(url);
  }

  plotfilters(
    key: string,
    reportId: string,
    DatabaseCode: string,
    userId: string,
    farmId: string,
    chcId: string,
    ccId: string,
    farmerId: string,
    userType: string,
    organization: string
  ): Observable<any> {

    const identifier = 'api/ReportApi/getReportFilterDetailsByReportidAndUserid'
    let url_getReportFilterDetailsByReportidAndUserid = this.BASE_URL+identifier+'?Userid='+userId+
    "&Reportid=" +
        reportId +
        "&farmId=" +
        farmId +
        "&chcId=" +
        chcId +
        "&ccId=" +
        ccId +
        "&farmerId=" +
        farmerId +
        "&userType=" +
        userType +
        "&organization=" +
        organization +
        "&Key=" +
        key +
        "&con=" +
        DatabaseCode

    
    return this.http.get(url_getReportFilterDetailsByReportidAndUserid);

  
    // return this.http.get('http://localhost:8100/assets/data/api2.json');
  }

  plotDataTable(
    userId: string,
    reportId: string,
    querystr: string,
    key: string,
    DatabaseCode: string,
    organization: string
  ): Observable<any> {

    const identifier = 'api/ReportAPI/getReportDataByReportidAndUserid'
    let url_getReportDataByReportidAndUserid = this.BASE_URL+identifier+'?Userid='+userId+
    "&Reportid=" +
        reportId +
        "&FilterDetails=" +
        querystr +
        "&organization=" +
        organization +
        "&Key=" +
        key +
        "&con=" +
        DatabaseCode

    return this.http.get(url_getReportDataByReportidAndUserid);
  }

  getStaticProductsFromRegister(){
    const url_getStaticProductsFromRegister = "https://localhost:44336/api/ManualProducts/getManualProducts"
    return this.http.get(url_getStaticProductsFromRegister)
  }


  transformDateToYYYYMMdd(inputDate:string):string{
      // let tempStrArry = inputDate.split('-')
      // let convertedDate=""
      // for (let i=0;i<tempStrArry.length;i++){
      //     convertedDate=tempStrArry[i]+'-'+convertedDate
      // }
      // convertedDate=convertedDate.slice(0,-1);
      let convertedDate=new DatePipe('en-US').transform(inputDate, 'yyyy-MM-dd');
      return convertedDate;
  }



getAllColumnsForReport(reportID:number){
let url_getAllColumnsForReport =this.BASE_URL+
                                'api/ReportApi/getAllColumnsForReport?ReportID='+
                                 reportID +
                                '&Key=' + this.key +
                                '&con=' + this.DatabaseCode;

  return this.http.get(url_getAllColumnsForReport)
            
}


  async updateColumnDetailsForUser(reportID:number,ColumnIDs){

    const url_updatecolumnDetailsForUser =
      this.BASE_URL +
      'api/ReportApi/updateReportColumnDetailsByUserID?UserId=' +
      this.getFarmerUserID() +
      '&ReportID='+reportID+
      '&columnIDs=' + ColumnIDs+
      '&Key=' +
      this.key +
      '&con=' +
      this.DatabaseCode;
      
      const formDataa = new FormData();
      let resultOfUpdate:any=0;
         await this.http.post(url_updatecolumnDetailsForUser, formDataa).toPromise().then(
          (m:any) =>{
            resultOfUpdate=m.length
          }
         )
        return resultOfUpdate;
  }



  async getFilterDropDownValues(filter,inputField){


    let chcID=0;
    let ccID=0;
    let farmerID=0;

    switch(filter.filterName){
      case 'Chilling Center' :
                              break;
      case 'Collection Center' : chcID=inputField;
                              break;
      case 'Farmer ID' : ccID= inputField;
                            break;
      default : return;
    }

  let dataForDropdown:any[]=[]

      const url_getFilterDetailsByFilterID =
      this.BASE_URL +
      'api/ReportApi/getReportFilterDetailsByFilterId?Userid=' +
      this.getFarmerUserID() +
      '&FilterId='+filter.filterId+
      '&farmId='+this.getFarmID()+
      '&chcId='+chcID+
      '&ccId=' + ccID+
      '&farmerId='+farmerID+
      '&userType='+this.getUserType()+
      '&organization=0'+
      '&Key=' +
      this.key +
      '&con=' +
      this.DatabaseCode;

      await this.http.get(url_getFilterDetailsByFilterID).toPromise().then((res:any)=>{
        dataForDropdown=res
                })


  return dataForDropdown;
     
}



}
